/* Portfolio Filters */
const portfolioItems = document.querySelectorAll(".portfolio-grid .portfolio-item-wrap");
const portfolioButtons = Array.from(document.querySelectorAll(".portfolio-filters button"));
const websitesFilter = document.querySelector('#websites-filter');



portfolioButtons.forEach(function(elem) {
    elem.addEventListener("click", filterPortfolioItems)
});


const simulateClick = (elem) => {
  // Create our event (with options)
  let evt = new MouseEvent('click', {
    bubbles: true,
    cancelable: true,
    view: window
  });
  // If cancelled, don't dispatch our event
  const canceled = !elem.dispatchEvent(evt);
};

simulateClick( websitesFilter );

//Just to make sure it does get the styling at the start.
websitesFilter.classList.add('active');

/******* *****/


/* Portfolio Filter function */
function filterPortfolioItems(e){
  const clickedButtonId = e.target.id;

  //Remove the styling at the start for this initially set filter
  document.querySelector('#websites-filter').classList.remove('active');

  for (const cur of portfolioItems) {
  	if (cur.className === "portfolio-item-wrap " + clickedButtonId
        || cur.className === "portfolio-item-wrap " + clickedButtonId + " hidden" 
        || cur.className === "portfolio-item-wrap " + clickedButtonId + " filter-description"
        || cur.className === "portfolio-item-wrap " + clickedButtonId + " filter-description hidden"
        ){
      setTimeout(function() {
        cur.style.display = "flex";
        cur.classList.remove("hidden");
      }, 500);
      continue;
    /* In case I add a showAll button
  	} else if( e.target.id === "showAll"){
      	cur.classList.remove("hidden");
        cur.style.position = "initial";
      continue; */
    }     
  		cur.classList.add("hidden");
      setTimeout(function() {
        cur.style.display = "none";
      }, 500); //Transition timing
  }
};


